import { Image, InsertDriveFileOutlined, VideoFile, AudioFile, PictureAsPdf, Article } from '@mui/icons-material'

export const getSize = (size:number) => {
  var sizes = [' Bytes', ' KB', ' MB', ' GB', ' TB', ' PB', ' EB', ' ZB', ' YB']

  for (var i = 1; i < sizes.length; i++) {
    if (size < Math.pow(1024, i)) 
    return (Math.round((size / Math.pow(
    1024, i - 1)) * 100) / 100) + sizes[i - 1]
  }
  return size
}

export const getIcon = (type:string) => {
  switch(type.split('/')[0]) {
    case 'image':
      return <Image color='action'/>
    case 'video':
      return <VideoFile color='action'/>
    case 'audio':
      return <AudioFile color='action'/>
    case 'text':
      return <Article color='action'/>
    case 'application':
      return type.split('/')[1] === 'pdf' ? <PictureAsPdf color='action'/> : <InsertDriveFileOutlined color='action'/>
    default: return <InsertDriveFileOutlined color='action'/>
  }
}

export const getNameType = (fileType:string) => fileType.split('/')[0]

export const differenceInDate = (currentDate:string | number, prevDate:string | number | undefined) => {
  if(prevDate === undefined) {
    return true
  } else {
    const current = new Date(currentDate)
    const prev = new Date(prevDate)
  
    const local = navigator.language
    const dateTransform = new Intl.DateTimeFormat(local)

    return dateTransform.format(prev) > dateTransform.format(current) ? true : false
  }
}

export const createCookie = (cookieName:string, value:any) => {
  document.cookie = `${cookieName}=${value}; expires=${new Date(Date.now() + 86400e3)}`
}

export const getCookie = (name:string) => {
  let matches = document.cookie.match(new RegExp(
    "(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"
  ))
  return matches ? decodeURIComponent(matches[1]) : undefined
}

export const setLocalStorage = (keyName:string, value:any) => {
  localStorage.setItem(keyName, JSON.stringify(value))
}

export const getLocalStorageItem = (keyName:string) => {
  return JSON.parse(localStorage.getItem(keyName)!)
}