import React, {useState, useEffect} from 'react'
import { Card, CardContent, Typography, LinearProgress, Box } from '@mui/material'
import { getSize } from '../helper/functions'
import { totalMemoryBite } from '../constants/app'
import { useAppSelector } from '../hooks/useAppSelector'

export const MemoryCard = () => {
  const {files} = useAppSelector(state=>state.files)

  const [lineValue, setLineValue] = useState(0)

  const currentSize = files.reduce((prev, next)=>{
    return prev + next.size
  }, 0)

  useEffect(()=>{
    setLineValue(+((currentSize / totalMemoryBite) * 100).toFixed(2))
  },[currentSize])
  
  return (
    <Card variant="outlined" sx={{border:'none'}}>
      <CardContent>
        <Typography sx={{mb:2}}>
          Места на диске
        </Typography>
        <LinearProgress variant="determinate" value={lineValue} sx={{mb:2}}/>
        <Box sx={{display:'flex', alignItems:'center', justifyContent:'space-between'}}>
          <Typography>
            {getSize(currentSize)}
          </Typography>
          <Typography>
            {getSize(totalMemoryBite)}
          </Typography>
        </Box>
      </CardContent>
    </Card>
  )
}