import React, {FC, useEffect} from 'react'
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, IconButton, Typography } from '@mui/material'
import { IModalUpLoad } from '../interface/IModalUpLoad'
import {Close} from '@mui/icons-material';
import { useAppSelector } from '../hooks/useAppSelector';
import { ModalUploadFiles } from './ModalUploadFiles';
import { useAppDispatch } from '../hooks/useAppDispatch';
import { setIsSendFiles } from '../store/reducers/uploadFileSlice';

export const ModalUpLoad:FC<IModalUpLoad> = (props) => {
  const {open, setOpen} = props
  const {files, isSendFiles} = useAppSelector(state=>state.uploadFiles)
  const dispatch = useAppDispatch()

  const sendFilesOnStorage = () => {
    dispatch(setIsSendFiles(true))
  }

  useEffect(() => {
    files.length === 0 && setOpen()
  }, [files.length])
  
  return (
    <Dialog
      open={open}
      onClose={isSendFiles ? undefined : setOpen}
      maxWidth={'md'}
      fullWidth
    >
      <DialogTitle
        sx={{display:'flex',alignItems:'center', justifyContent:'space-between'}}
      >
        Файлы на загрузку
        <IconButton onClick={setOpen} disabled={isSendFiles ? true : false}>
          <Close/>
        </IconButton>
      </DialogTitle>
      <DialogContent dividers sx={{pb:0}}>
        {files.map(item=><ModalUploadFiles
          key={item.name}
          name={item.name}
          size={item.size}
          type={item.type}
          item={item}
        />)}
      </DialogContent>
      <DialogActions>
        <Typography sx={{mr:'auto', pl:2}}>
          Максимум в день можно загрузить до 1 GB
        </Typography>
        <Button onClick={setOpen} disabled={isSendFiles ? true : false}>
          Отмена
        </Button>
        <Button onClick={sendFilesOnStorage} disabled={isSendFiles}>
          {!isSendFiles
            ? 'Загрузить'
            : 'Загрузка...'
          }
        </Button>
      </DialogActions>
    </Dialog>
  )
}