import React from 'react'
import { Box, Typography } from '@mui/material'

export const MobileWrapper = () => {

  const style = {
    position: 'fixed',
    background: '#fff',
    zIndex: '999999',
    width: '100%',
    height: '100%',
    left: '0',
    top: '0',
    alignItems:'center',
    justifyContent:'center'
  }

  return (
    <Box
      sx={{...style}}
      display={{sm:'flex', md:'none'}}
    >
      <Typography variant='h5' align='center'>
          Для мобильной версии у нас разрабатывается приложение
          <br />
          Пожалуйста, зайдите на сайт, через компьютер
      </Typography>
    </Box>
  )
}