import React, {FC, useState} from 'react'
import { Box, Button, Checkbox, Dialog, DialogContent, DialogTitle, FormControlLabel, Typography } from '@mui/material'
import { IModalUpLoad } from '../interface/IModalUpLoad'
import { useAppDispatch } from '../hooks/useAppDispatch'
import { setAgree } from '../store/reducers/userSlice'
import { createCookie } from '../helper/functions'
import { cookieIsAgreeName } from '../constants/app'

export const Placeholder:FC<IModalUpLoad> = (props) => {
  const {open, setOpen} = props
  const dispatch = useAppDispatch()

  const [checked, setChecked] = useState(false)

  const agreeSubmit = () => {
    if(checked) {
      dispatch(setAgree(true))
      createCookie(cookieIsAgreeName, true)
    }
  }

  return (
    <Dialog
      open={open}
      onClose={setOpen}
      maxWidth={'md'}
      fullWidth
    >
      <DialogTitle>
        Внимательно ознакомьтесь с правилами!
      </DialogTitle>
      <DialogContent sx={{pb:1}}>
        <Typography>
          Внимание! Это демо версия сайта, на нём нет авторизации.
          <br />
          Все файлы которые вы отправите, может посмотреть и скачать любой человек.
          <br />
          Пожалуйста, не отправляйте важную для вас информацию на хостинг.
        </Typography>
      </DialogContent>
      <Box sx={{pl:2, pr:2, pt:0, pb:2, display:'flex', alignItems:'center', justifyContent:'space-between'}}>
        <FormControlLabel
          control={
            <Checkbox checked={checked} onChange={()=>setChecked(prev=>!prev)} />
          }
          label="Я ознакомился с правилами и принимаю всю ответственность на себя"
        />
        <Button onClick={agreeSubmit} disabled={!checked}>
          Принять
        </Button>
      </Box>
    </Dialog>
  )
}