import React from 'react'
import { Route, Routes } from 'react-router-dom'
import { routesArr } from '../routes/routes'

export const AppRouter = () => {
  return (
    <Routes>
      {routesArr.map(({url, component: Component})=><Route key={url} path={url} element={<Component/>}/>)}
    </Routes>
  )
}