import React, {FC, useState} from 'react'
import { Download, GradeRounded, OpenInNew, Visibility } from '@mui/icons-material'
import { Box, IconButton, Paper, Tooltip, Typography } from '@mui/material'
import { getDownloadURL, ref } from 'firebase/storage'
import { storage } from '../firebase'
import { getIcon, getNameType, getSize } from '../helper/functions'
import { useAppDispatch } from '../hooks/useAppDispatch'
import { ITableFile } from '../interface/ITableFile'
import { removeFav } from '../store/reducers/favSlice'
import { FileMedia } from './FileMedia'

export const FavItem:FC<ITableFile> = (props) => {
  const {
    id,
    title,
    url,
    size,
    timeCreated,
    contentType,
  } = props
  const dispatch = useAppDispatch()
  const [isShowMedia, setShowMedia] = useState(false)

  const date = new Date(timeCreated)
  const local = navigator.language
  const currentDate = new Intl.DateTimeFormat(local)

  const dowloadFile = (name:string, e:React.MouseEvent) => {
    e.stopPropagation()
    getDownloadURL(ref(storage, name))
    .then((url) => {
      const xhr = new XMLHttpRequest()
      xhr.responseType = 'blob'
      xhr.onload = (event) => {
        const blob = xhr.response

        const urls = window.URL.createObjectURL(
          new Blob([blob]),
        )
        const link = document.createElement('a')!
        link.href = urls
        link.setAttribute(
          'download',
          title,
        )
        document.body.appendChild(link)
        link.click()
        link.parentNode!.removeChild(link)
      }
      xhr.open('GET', url)
      xhr.send()
    })
  }

  const removeToFavourites = (e:React.MouseEvent) => {
    e.stopPropagation()
    dispatch(removeFav(title))
  }

  const showFile = (url:string, e:React.MouseEvent) => {
    e.stopPropagation()
    window.open(url)
  }

  return (
    <>
    <Paper
      sx={{
        display:'flex',
        alignItems:'center',
        justifyContent:'space-between',
        mb:2,
        p:2,
        cursor:getNameType(contentType) === 'image' ? 'pointer' : 'default'
      }}
      onClick={getNameType(contentType) === 'image' ? ()=>setShowMedia(true) : undefined}
    >
      <Box sx={{display:'flex', alignItems:'center', overflow:'hidden'}}>
        {getIcon(contentType)}
        <Typography sx={{ml:2}} noWrap>
          {title}
        </Typography>
      </Box>
      <Box sx={{display:'flex', alignItems:'center'}}>
        <Typography>
          {currentDate.format(date)}
        </Typography>
        <Typography sx={{ml:2,mr:1}}>
          {getSize(size)}
        </Typography>
        {title.slice(title.length-3, title.length) === 'pdf' ?
          <Tooltip title='Открыть'>
            <IconButton onClick={(e)=>showFile(url, e)}>
              <OpenInNew/>
            </IconButton>
          </Tooltip>
        : null}
        {getNameType(contentType) === 'image' ?
          <Tooltip title='Посмотреть изображение'>
            <IconButton onClick={()=>setShowMedia(true)}>
              <Visibility/>
            </IconButton>
          </Tooltip>
        : null}
        <Tooltip title='Убрать из избранного'>
          <IconButton onClick={(e)=>removeToFavourites(e)}>
            <GradeRounded/>
          </IconButton>
        </Tooltip>
        <Tooltip title='Скачать'>
          <IconButton onClick={(e)=>dowloadFile('/'+title, e)}>
            <Download/>
          </IconButton>
        </Tooltip>
      </Box>
    </Paper>
    {isShowMedia &&
      <FileMedia open={isShowMedia} setOpen={()=>setShowMedia(false)} file={props} />
    }
    </>
  )
}