import React, {useState, useRef} from 'react'
import { Button,Box, Card, CardContent, CardActions, Typography } from '@mui/material'
import { FileUploadOutlined } from '@mui/icons-material'
import { blue, grey } from '@mui/material/colors'
import { IUploadFile } from '../interface/IUploadFile'
import { setFiles, setIsShowModal } from '../store/reducers/uploadFileSlice'
import { useAppDispatch } from '../hooks/useAppDispatch'

export const UploadSide = () => {
  const dispatch = useAppDispatch()

  const inputLoad = useRef<HTMLInputElement>(null)
  const [onDrag, setOnDrag] = useState(false)

  const bgColor = grey[200]
  const borderColorDash = grey[400]
  const borderColorActive = blue[700]
  
  const uploadAreaStyle = {
    height:'150px',
    border: onDrag ? `2px solid ${borderColorActive}` : `1px dashed ${borderColorDash}`,
    cursor:'pointer',
    display:'flex',
    alignItems:'center',
    justifyContent:'center',
    backgroundColor:bgColor,
    borderRadius:'4px',
    boxSizing:'border-box',
    '&:hover': {
      border: `2px solid ${borderColorActive}`,
    }
  }

  const dragStrat = (e:React.DragEvent<HTMLDivElement>) => {
    e.preventDefault()
    e.stopPropagation()
    setOnDrag(true)
  }

  const dragLeave = (e:React.DragEvent<HTMLDivElement>) => {
    e.preventDefault()
    e.stopPropagation()
    setOnDrag(false)
  }

  const onDrop = (e:any) => {
    e.preventDefault()
    e.stopPropagation()
    getFiles(e.dataTransfer.files)
    setOnDrag(false)
  }

  const changeInput = (e:any) => {
    if(e.target.files.length > 0){
      getFiles(e.target.files)
      const input:HTMLInputElement = document.getElementById("input-field") as HTMLInputElement
      input.value = ''
    }  
  }

  const getFiles = (array:IUploadFile[]) => {
    dispatch(setFiles([...array]))
    dispatch(setIsShowModal(true))
  }

  return (
    <Card variant="outlined" sx={{border:'none'}}>
      <CardContent>
        <Box
          sx={{...uploadAreaStyle}}
          onClick={()=>inputLoad.current?.click()}
          onDragStart={e=>dragStrat(e)}
          onDragOver={e=>dragStrat(e)}
          onDragLeave={e=>dragLeave(e)}
          onDrop={e=>onDrop(e)}
        >
          <Box sx={{display:'flex', flexDirection:'column', alignItems:'center', pointerEvents:'none'}}>
            <FileUploadOutlined fontSize='large' sx={{mb:2}}/>
            <Typography variant='subtitle1' align='center'>
              Перенесите Файлы
            </Typography>
          </Box>
        </Box>
      </CardContent>
      <CardActions sx={{p:2,pt:0}}>
        <Button sx={{width:'100%'}} variant="contained" component='label'>
          загрузить файлы
          <input
            id="input-field"
            ref={inputLoad}
            type="file"
            hidden
            multiple
            onChange={(e:React.ChangeEvent<HTMLInputElement>)=>changeInput(e)}
           />
        </Button>
      </CardActions>
    </Card>
  )
}