import React, {FC} from 'react'
import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@mui/material'
import { IModalFavAlert } from '../interface/IModalFavAlert'

export const ModalFavAlert:FC<IModalFavAlert> = (props) => {
  const {open, setOpen, onClick} = props
  return (
    <Dialog
      open={open}
      onClose={setOpen}
    >
      <DialogTitle>
        Нельзя удалить избранный файл
      </DialogTitle>
      <DialogContent>
        <DialogContentText>
          Вы действительно хотите удалить файл? Это действие невозможно будет отменить
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button variant='outlined' onClick={setOpen}>Отмена</Button>
        <Button variant='contained' color='error' onClick={onClick}>Удалить</Button>
      </DialogActions>
    </Dialog>
  )
}