import React, {useState} from 'react'
import { useAppSelector } from '../hooks/useAppSelector'
import { useAppDispatch } from '../hooks/useAppDispatch'
import { TableFile } from '../components/TableFile'
import { sortHandler } from '../store/reducers/fileSlice'
import { ITableCol } from '../interface/ITableCol'
import { Box, CircularProgress, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TableSortLabel, Typography } from '@mui/material'

export const MyFiles = () => {
  const dispatch = useAppDispatch()
  const {files, isLoading} = useAppSelector(state=>state.files)

  const [sortId, setSortId] = useState<number | null>(null)

  const [tableCol, setTableCol] = useState<ITableCol[]>([
    {category:'Имя', sortType:'title', isSortUp: true},
    {category:'Дата Загрузки', sortType:'timeCreated', isSortUp:true},
    {category:'Размер', sortType:'size', isSortUp:true},
  ])

  const createSortHandler = (ind:number, type:'title' | 'size' | 'timeCreated', isSortUp:boolean) => {
    setSortId(prev=>ind)
    
    if(sortId === ind){
      setTableCol(prev=>[...prev.map((col,i)=>i===ind ? {...col, isSortUp:!col.isSortUp} : {...col, isSortUp:true})])
      dispatch(sortHandler({type, isSortUp:!isSortUp}))
    } else dispatch(sortHandler({type, isSortUp}))
  }
  
  return (
    <div>
      <Typography variant='h5' sx={{mb:2}}>
        Мои Файлы
      </Typography>
      {!isLoading && files.length === 0 &&
        <Typography align='center' variant='h5'>
          Хранилище сейчас пусто
          <br />
          Отправьте файлы для надежного хранения
        </Typography>
      }
      {isLoading &&
        <Box sx={{display:'flex', justifyContent:'center'}}>
          <CircularProgress/>
        </Box>
      }
      {!!files.length && 
        <TableContainer component={Paper} sx={{maxHeight:'calc(100vh - 183px)'}}>
          <Table stickyHeader>
            <TableHead>
              <TableRow>
                {tableCol.map((item,i)=>
                  <TableCell key={item.category} sx={{position:'relative'}}>
                    <TableSortLabel
                      active={i === sortId}
                      direction={item.isSortUp ? 'asc' : 'desc'}
                      onClick={()=>createSortHandler(i, item.sortType, item.isSortUp)}
                    >
                      {item.category}
                    </TableSortLabel>
                  </TableCell>)}
              </TableRow>
            </TableHead>
            <TableBody>
              {files.map(file=>
                <TableFile
                  key={file.id}
                  id={file.id}
                  title={file.title}
                  url={file.url}
                  size={file.size}
                  timeCreated={file.timeCreated}
                  contentType={file.contentType}
                />)}
            </TableBody>
          </Table>
        </TableContainer>
      }
    </div>
  )
}