import React, {FC, useEffect, useState} from 'react'
import { Box, CircularProgress, Dialog, DialogContent, DialogTitle, IconButton, Typography } from '@mui/material'
import { IFileMedia } from '../interface/IFileMedia'
import { Close } from '@mui/icons-material'

export const FileMedia:FC<IFileMedia> = (props) => {
  const {open, setOpen, file} = props
  const {title, url} = file
  const [isImgLoading, setImgLoading] = useState(false)

  const preloadImage = (src: string) => {
    setImgLoading(true)
    return new Promise((resolve, reject) => {
      const img = new Image()
      img.onload = function() {
        resolve(img)
        setImgLoading(false)
      }
      img.onerror = img.onabort = function() {
        reject(src)
        setImgLoading(false)
      }
      img.src = src
    })
  }

  useEffect(()=>{
    preloadImage(url)
  },[])

  return (
    <Dialog
      open={open}
      onClose={setOpen}
      maxWidth={'md'}
      fullWidth
    >
      <DialogTitle
        sx={{display:'flex',alignItems:'center', justifyContent:'space-between'}}
        variant='h6'
        component={'div'}
      >
        <Typography variant='h6' component={'h6'} noWrap>
          {title}
        </Typography>
        <IconButton onClick={setOpen}>
          <Close/>
        </IconButton>
      </DialogTitle>
      <DialogContent>
        {isImgLoading 
          ? <Box sx={{display:'flex', justifyContent:'center', pb:2}}>
              <CircularProgress/>
            </Box>
          : <img
              style={{objectFit: 'contain', maxHeight:'350px', display:'block', margin:'0 auto'}}
              src={url}
              alt={title}
            />
        }
      </DialogContent>
    </Dialog>
  )
}