import { createSlice, PayloadAction } from "@reduxjs/toolkit"
import { ITableFile } from "../../interface/ITableFile"

interface initialState {
  files:ITableFile[]
}
const initialState:initialState = {
  files:[]
}

const trashSlice = createSlice({
  name:'trash',
  initialState,
  reducers:{
    addTrash(state, action:PayloadAction<ITableFile>){
      state.files = [...state.files, action.payload]
    },
    removeTrash(state, action:PayloadAction<ITableFile['title']>){
      state.files = state.files.filter(file=>file.title !== action.payload)
    }
  }
})
export const {addTrash, removeTrash} = trashSlice.actions
export default trashSlice.reducer