import { createSlice, PayloadAction } from "@reduxjs/toolkit"
import { ITableFile } from "../../interface/ITableFile"

interface initialState {
  files:ITableFile[];
  isLoading:boolean;
}
const initialState:initialState = {
  files:[],
  isLoading:false
}

const fileSlice = createSlice({
  name:'files',
  initialState,
  reducers:{
    addFiles(state, action:PayloadAction<ITableFile>){
      state.files = [...state.files, action.payload]
    },
    removeFile(state, action:PayloadAction<ITableFile['title']>){
      state.files = state.files.filter(file=>file.title !== action.payload)
    },
    sortHandler(state, action:PayloadAction<{type: 'title' | 'size' | 'timeCreated', isSortUp:boolean}>){
      action.payload.isSortUp
      ? state.files.sort((a,b)=>a[action.payload.type] < b[action.payload.type] ? 1 : -1)
      : state.files.sort((a,b)=>a[action.payload.type] > b[action.payload.type] ? 1 : -1)
    },
    setLoading(state, action:PayloadAction<boolean>){
      state.isLoading = action.payload
    }
  }
})
export const {addFiles, removeFile, sortHandler, setLoading} = fileSlice.actions
export default fileSlice.reducer