import React, {FC, useState, useEffect} from 'react'
import { IUploadFile } from '../interface/IUploadFile'
import { useAppDispatch } from '../hooks/useAppDispatch'
import { deleteUploadFile, setIsSendFiles, setIsShowAlert, setIsShowModal } from '../store/reducers/uploadFileSlice'
import { getIcon, getSize } from '../helper/functions'
import { IconButton, Paper, Typography, Box, LinearProgress } from '@mui/material'
import { Delete } from '@mui/icons-material'
import { getDownloadURL, getMetadata, listAll, ref, uploadBytesResumable } from 'firebase/storage'
import { storage } from '../firebase'
import { useAppSelector } from '../hooks/useAppSelector'
import { ITableFile } from '../interface/ITableFile'
import { addFiles } from '../store/reducers/fileSlice'

export const ModalUploadFiles:FC<IUploadFile> = (props) => {
  const {name, size, type, item} = props
  const dispatch = useAppDispatch()

  const {isSendFiles, files} = useAppSelector(state=>state.uploadFiles)
  const {files:filesPage} = useAppSelector(state=>state.files)

  const mostBigFile = [...files].sort((a,b)=>a.size < b.size ? 1 : -1)[0]

  const [progress, setProgress] = useState(0)

  const uploadFileOnStorage = () => {
    const filesRef = ref(storage, name)
    const uploadTask = uploadBytesResumable(filesRef, item)

    uploadTask.on('state_changed',
      (snapshot)=>{
        const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100
        if(isNaN(progress)){
          setProgress(100)
        } else setProgress(prev=>progress)
      }
    )
  }

  const getFilesOnPage = () => {
    const filesListRef = ref(storage, '/')
    listAll(filesListRef)
    .then(res=>{
      res.items.map((file:any)=>{
        const filetRef = ref(storage, '/'+file._location.path_)
        getDownloadURL(filetRef)
          .then((url) => {
            getMetadata(filetRef)
              .then((metadata) => {
                if(filesPage.filter(itemFile=>itemFile.title === file._location.path_).length === 0){
                  const item:ITableFile = {
                    id: file._location.path_,
                    title: file._location.path_,
                    url:url,
                    size:metadata.size,
                    timeCreated:metadata.timeCreated,
                    contentType:metadata.contentType!,
                  }
                  dispatch(addFiles(item))
                }
              })
        })
      })
    })
  }

  useEffect(() => {
    if(isSendFiles){
      uploadFileOnStorage()
    } else return
  }, [isSendFiles])

  useEffect(()=>{
    if(mostBigFile.name === name){
      if(progress === 100) {
        
        const timer = setTimeout(()=>{
          dispatch(setIsShowAlert(true))
          getFilesOnPage()
          dispatch(setIsShowModal(false))
          dispatch(setIsSendFiles(false))
        }, 750)

        return () => {
          clearTimeout(timer)
        }
      }
    } else return
  },[mostBigFile, progress])

  return (
    <Paper
      variant='outlined'
      sx={{mb:2}}
    >
      <Box sx={{display:"flex", alignItems:'center', justifyContent:'space-between', p:1}}>
        <Box sx={{display:"flex", alignItems:'center', overflow:'hidden'}}>
          {getIcon(type)}
          <Typography sx={{ml:2}} noWrap>
            {name}
          </Typography>
        </Box>
        <Box sx={{display:"flex", alignItems:'center', ml:2}}>
          <Typography sx={{mr:2, display:"flex", alignItems:'center', width:'max-content'}}>
            {getSize(size)}
          </Typography>
          <IconButton onClick={()=>dispatch(deleteUploadFile(name))} disabled={isSendFiles ? true : false}>
            <Delete/>
          </IconButton>
        </Box>
      </Box>
      <LinearProgress variant="determinate" value={progress}/>
    </Paper>
  )
}