import React from 'react'
import { ROUTES } from '../routes/routes'
import { IAsideItem } from '../interface/IAsideItem'
import { Box, Divider, List, Paper, Typography } from '@mui/material'
import { AsideItem } from './AsideItem'
import {DeleteOutlineRounded , StarOutlineRounded, InsertDriveFileOutlined, HistoryOutlined, CloudDownloadOutlined} from '@mui/icons-material'
import { UploadSide } from './UploadSide'

export const Aside = () => {
  const asideItems:IAsideItem[] = [
    {title:'Мои Файлы', url:ROUTES.HOME, icon:<InsertDriveFileOutlined/>},
    {title:'Недавние', url:ROUTES.RECENT, icon:<HistoryOutlined/>},
    {title:'Избранное', url:ROUTES.FAVOURITES, icon:<StarOutlineRounded/>},
    // {title:'Корзина', url:ROUTES.TRASH, icon:<DeleteOutlineRounded/>},
  ]
  
  return (
    <Paper sx={{height:'100vh', display:"flex", flexDirection:'column', pt:2, boxSizing:'border-box'}}>
      <Box sx={{display:'flex', alignItems:'center', pl:2}}>
        <Typography variant='h5' component={'h1'} sx={{mr:2}} typography={{md:'subtitle1', lg:'subtitle1', xl:'h5'}}>
          React Cloud
        </Typography>
        <CloudDownloadOutlined fontSize={'large'}/>
      </Box>
      <List>
        {asideItems.map(({title, url, icon})=>
          <AsideItem
            key={title}
            title={title}
            url={url}
            icon={icon}
          />
          )}
      </List>
      <Box sx={{mt:'auto'}}>
        <Divider/>
        <UploadSide/>
      </Box>
    </Paper>
  )
}