import React, { FC } from "react"
import { Link, useLocation } from "react-router-dom"
import { IAsideItem } from "../interface/IAsideItem"
import { ListItem, ListItemButton, ListItemIcon, ListItemText} from "@mui/material"

export const AsideItem: FC<IAsideItem> = (props) => {
  const { title, url, icon } = props
  const location = useLocation()

  const startInd = location.pathname.indexOf(url)
  const currentAppUrl = location.pathname.slice(startInd, location.pathname.length)

  return (
    <ListItem>
      <ListItemButton
        component={Link}
        to={url}
        selected={url === currentAppUrl ? true : false}
      >
        <ListItemIcon>{icon}</ListItemIcon>
        <ListItemText primary={title} />
      </ListItemButton>
    </ListItem>
  )
}