import React from 'react'
import { Avatar, Box, Divider, List, ListItem, ListItemAvatar, ListItemText, Paper, Typography } from '@mui/material'
import { MemoryCard } from './MemoryCard'
import { ICategoriesItem } from '../interface/ICategoriesItem'
import { useAppSelector } from '../hooks/useAppSelector'
import { getSize } from '../helper/functions'

export const MemoryBar = () => {
  const {files} = useAppSelector(state=>state.files)

  const categoriesItems:ICategoriesItem[] = []

  const filterOnTypes = (types:string[]) => {
    const currentArr:any[] = []
    types.map(item=>files.map(file=>file.contentType.split('/')[0] === item ? currentArr.push(file) : file))
    return currentArr
  }

  const getCategories = () => {
    const images = {
      name:'Изображения',
      array: filterOnTypes(['image'])
    }
    const media = {
      name:'Медиа',
      array: filterOnTypes(['video', 'audio'])
    }
    const docs = {
      name:'Документы',
      array: filterOnTypes(['text','application'])
    }

    const allCategories = [images, media, docs]
      .filter(item=>item.array.length!!)
      .sort((a,b)=>b.array.reduce((prev,next)=>prev + next.size, 0) - a.array.reduce((prev,next)=>prev + next.size, 0))

    allCategories.map(item=>{
      const categoriesItem:ICategoriesItem = {
        type:item.name,
        files:item.array.length,
        size:getSize(
          item.array.reduce((prev,next)=>{
          return prev + next.size
        },0)
        ) 
      }
      categoriesItems.push(categoriesItem)
    })
  }
  getCategories()

  return (
    <Paper sx={{height:'100vh', pt:2, boxSizing:'border-box'}}>
      <Box sx={{p:2,pt:0}}>
        <Typography variant='h5' component={'h2'} typography={{md:'subtitle1', lg:'subtitle1', xl:'h5'}}>
          Остаток Хранилища
        </Typography>
      </Box>
      <Divider/>
      <MemoryCard/>
      <Divider/>
      <List>
        {categoriesItems.map(item=>
          <ListItem
            key={item.type}
            secondaryAction={
              <ListItemText primary={
                <Typography  typography={{md:'body2', lg:'body2', xl:'body1'}}>
                  {item.size}
                </Typography>
                }
              />
            }
          >
            <ListItemAvatar>
              <Avatar>
                {item.type[0].toUpperCase()}
              </Avatar>
            </ListItemAvatar>
            <ListItemText
              primary={<Typography typography={{md:'body2', lg:'body2', xl:'body1'}}>{item.type}</Typography>}
              secondary={<Typography color={'GrayText'} typography={{md:'body2', lg:'body2', xl:'body1'}}>{`${item.files} файлов`}</Typography>}
            />
          </ListItem>
        )}
      </List>
    </Paper>
  )
}