import { createSlice, PayloadAction } from "@reduxjs/toolkit"
import { localStorageFavName } from "../../constants/app"
import { getLocalStorageItem, setLocalStorage } from "../../helper/functions"
import { ITableFile } from "../../interface/ITableFile"


interface initialState {
  files:ITableFile[]
}
const initialState:initialState = {
  files: getLocalStorageItem(localStorageFavName) ? getLocalStorageItem(localStorageFavName) : []
}

const favSlice = createSlice({
  name:'fav',
  initialState,
  reducers:{
    addFav(state, action:PayloadAction<ITableFile>){
      state.files = [...state.files, action.payload]
      setLocalStorage(localStorageFavName, state.files)
    },
    removeFav(state, action:PayloadAction<ITableFile['title']>){
      state.files = state.files.filter(file=>file.title !== action.payload)
      setLocalStorage(localStorageFavName, state.files)
    }
  }
})
export const {addFav, removeFav} = favSlice.actions
export default favSlice.reducer