import React from 'react'
import { Box, CircularProgress, Typography } from '@mui/material'
import { RecentFile } from '../components/RecentFile'
import { ITableFile } from '../interface/ITableFile'
import { differenceInDate } from '../helper/functions'
import { useAppSelector } from '../hooks/useAppSelector'

export const Recent = () => {
  const {files, isLoading} = useAppSelector(state=>state.files)
  const currntFiles:ITableFile[] =  [...files]

  return (
    <div>
      <Typography variant='h5' sx={{mb:2}}>
        Недавно загруженные файлы
      </Typography>
      {isLoading &&
       <Box sx={{display:'flex', justifyContent:'center'}}>
          <CircularProgress/>
        </Box>}
        {!isLoading && files.length === 0 &&
          <Typography variant='h5' align='center'>
            Нет загруженных файлов
          </Typography>
        }
      <Box sx={{overflow:'auto', height:'calc(100vh - 183px)'}}>
        {!!currntFiles.length &&
          currntFiles.sort((a,b)=>a.timeCreated < b.timeCreated ? 1 : -1)
            .map((item, i, arr)=>
              <RecentFile
                key={item.id}
                id={item.id}
                title={item.title}
                url={item.url}
                size={item.size}
                timeCreated={item.timeCreated}
                contentType={item.contentType}
                isNewDay={differenceInDate(item.timeCreated, arr[i - 1] === undefined ? undefined : arr[i - 1].timeCreated)}
              />)
          }
      </Box>
    </div>
  )
}