import React, {FC, useEffect, useState} from 'react'
import { ITableFile } from '../interface/ITableFile'
import { storage } from '../firebase'
import { deleteObject, getDownloadURL, ref } from 'firebase/storage'
import { getIcon, getNameType, getSize } from '../helper/functions'
import {Download, Delete, OpenInNew, GradeRounded, StarOutlineRounded, Visibility, MoreVert} from '@mui/icons-material'
import { Box, IconButton, Menu, MenuItem, TableCell, TableRow, Tooltip, Typography } from '@mui/material'
import { useAppDispatch } from '../hooks/useAppDispatch'
import { removeFile } from '../store/reducers/fileSlice'
import { useAppSelector } from '../hooks/useAppSelector'
import { addFav, removeFav } from '../store/reducers/favSlice'
import { FileMedia } from './FileMedia'
import { ModalFavAlert } from './ModalFavAlert'

export const TableFile:FC<ITableFile> = (props) => {
  const {
    id,
    title,
    url,
    size,
    timeCreated,
    contentType,
  } = props

  const dispatch = useAppDispatch()
  const {files} = useAppSelector(state=>state.fav)
  
  const [isShowMedia, setShowMedia] = useState(false)

  const [isShowFavAlert, setShowFavAlert] = useState(false)

  const [favourites, setFavourites] = useState(false)
  const findIsFavourites = () => files.map(file=>file.id === id && setFavourites(true))

  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null)
  const open = Boolean(anchorEl)

  const date = new Date(timeCreated)
  const local = navigator.language
  const currentDate = new Intl.DateTimeFormat(local)

  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    event.stopPropagation()
    setAnchorEl(event.currentTarget)
  }
  const handleClose = (e:React.MouseEvent) => {
    e.stopPropagation()
    setAnchorEl(null)
  }

  const showMenu = (e:React.MouseEvent<HTMLElement>) => {
    e.stopPropagation()
    handleClick(e)
  }

  const dowloadFile = (name:string, e:React.MouseEvent) => {
    e.stopPropagation()
    getDownloadURL(ref(storage, name))
      .then((url) => {
        const xhr = new XMLHttpRequest()
        xhr.responseType = 'blob'
        xhr.onload = (event) => {
          const blob = xhr.response

          const urls = window.URL.createObjectURL(
            new Blob([blob]),
          )
          const link = document.createElement('a')!
          link.href = urls
          link.setAttribute(
            'download',
            title,
          )
          document.body.appendChild(link)
          link.click()
          link.parentNode!.removeChild(link)
        }
        xhr.open('GET', url)
        xhr.send()
      })
    handleClose(e)
  }
  
  const addToFavourites = (e:React.MouseEvent) => {
    e.stopPropagation()
    dispatch(addFav(props))
    setFavourites(true)
    handleClose(e)
  }

  const removeToFavourites = (e:React.MouseEvent) => {
    e.stopPropagation()
    dispatch(removeFav(title))
    setFavourites(false)
    handleClose(e)
  }

  const deletedFile = (url:string, e:React.MouseEvent) => {
    e.stopPropagation()
    if(favourites){
      setShowFavAlert(true)
      handleClose(e)
    } else {
      dispatch(removeFile(url))
      const deletedRef = ref(storage, '/'+url)
      deleteObject(deletedRef)
      handleClose(e)
    }
  }

  const deletedFileInAlert = () => {
    dispatch(removeFile(title))
    const deletedRef = ref(storage, '/' + title)
    deleteObject(deletedRef)
    dispatch(removeFav(title))
  }

  const showModalImage = (e:React.MouseEvent) => {
    e.stopPropagation()
    setShowMedia(true)
    handleClose(e)
  }

  const showFile = (url:string, e:React.MouseEvent) => {
    e.stopPropagation()
    window.open(url)
    handleClose(e)
  }

  useEffect(()=>{
    findIsFavourites()
  },[])

  return (
    <>
    <TableRow
      onClick={getNameType(contentType) === 'image' ? ()=>setShowMedia(true) : undefined}
      sx={{cursor:getNameType(contentType) === 'image' ? 'pointer' : 'default'}}
    >
      <TableCell>
        <Box sx={{display:'flex', alignItems:"center"}}>
          {getIcon(contentType)}
          <Typography sx={{ml:2}}>
            {title}
          </Typography>
        </Box>
      </TableCell>
      <TableCell>{currentDate.format(date)}</TableCell>
      <TableCell>
        <Box sx={{display:'flex', justifyContent:'space-between', alignItems:"center"}}>
          {getSize(size)}
          <Tooltip title='Все действия'>
            <IconButton onClick={(e)=>showMenu(e)}>
              <MoreVert/>
            </IconButton>
          </Tooltip>
        </Box>
        <Menu
          anchorEl={anchorEl}
          open={open}
          onClose={handleClose}
          anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
          transformOrigin={{ vertical: 'top', horizontal: 'right' }}
        >
          {title.slice(title.length-3, title.length) === 'pdf' ?
            <MenuItem sx={{p:1}} onClick={(e)=>showFile(url, e)}>
              <OpenInNew color='action' sx={{mr:1}}/>
              Открыть в новой вкладке
            </MenuItem>
          : null}
          {getNameType(contentType) === 'image' ?
            <MenuItem sx={{p:1}} onClick={(e)=>showModalImage(e)}>
              <Visibility color='action' sx={{mr:1}}/>
              Посмотреть изображение
            </MenuItem>
          : null}
          {favourites 
            ? <MenuItem sx={{p:1}} onClick={(e)=>removeToFavourites(e)}>
                <GradeRounded color='action' sx={{mr:1}}/>
                Убрать из избранного
              </MenuItem>
            : <MenuItem sx={{p:1}} onClick={(e)=>addToFavourites(e)}>
                <StarOutlineRounded color='action' sx={{mr:1}}/>
                Добавить в избранное
              </MenuItem>
          }
          <MenuItem sx={{p:1}} onClick={(e)=>deletedFile(title, e)}>
            <Delete color='action' sx={{mr:1}}/>
            Удалить
          </MenuItem>
          <MenuItem sx={{p:1}} onClick={(e)=>dowloadFile('/'+title, e)}>
            <Download color='action' sx={{mr:1}}/>
            Скачать
          </MenuItem>
        </Menu>
      </TableCell>
    </TableRow>
    {isShowMedia &&
      <FileMedia open={isShowMedia} setOpen={()=>setShowMedia(false)} file={props}/>
    }
    {isShowFavAlert &&
      <ModalFavAlert open={isShowFavAlert} setOpen={()=>setShowFavAlert(false)} onClick={deletedFileInAlert}/>
    }
    </>
  )
}