import { createSlice, PayloadAction } from "@reduxjs/toolkit"
import { IUploadFile } from "../../interface/IUploadFile"

interface initialState {
  files:IUploadFile[];
  isModalShow:boolean;
  isShowAlert:boolean;
  isSendFiles:boolean;
}
const initialState:initialState = {
  files:[],
  isModalShow:false,
  isShowAlert:false,
  isSendFiles:false,
}

const uploadFileSlice = createSlice({
  name:'uploadFiles',
  initialState,
  reducers:{
    setFiles(state, action:PayloadAction<IUploadFile[]>){
      state.files = [...action.payload]
    },
    deleteUploadFile(state, action:PayloadAction<IUploadFile['name']>){
      state.files = state.files.filter(item=>item.name !== action.payload)
    },
    setIsShowModal(state, action:PayloadAction<boolean>){
      state.isModalShow = action.payload
    },
    setIsShowAlert(state, action:PayloadAction<boolean>){
      state.isShowAlert = action.payload
    },
    setIsSendFiles(state, action:PayloadAction<boolean>) {
      state.isSendFiles = action.payload
    },
  }
})
export const {
  setFiles,
  deleteUploadFile,
  setIsShowModal,
  setIsShowAlert,
  setIsSendFiles,
} = uploadFileSlice.actions
export default uploadFileSlice.reducer