import React, {FC} from 'react'
import { Box, IconButton, Paper, Tooltip, Typography } from '@mui/material'
import {Download} from '@mui/icons-material';
import { IRecentFile } from '../interface/IRecentFile'
import { getIcon, getSize } from '../helper/functions'
import { getDownloadURL, ref } from 'firebase/storage';
import { storage } from '../firebase';
import { SystemDate } from './SystemDate';

export const RecentFile:FC<IRecentFile> = (props) => {
  const {
    id,
    title,
    size,
    url,
    timeCreated,
    contentType,
    isNewDay
  } = props

  const date = new Date(timeCreated)
  const local = navigator.language
  const currentDate = new Intl.DateTimeFormat(local)

  const dowloadFile = (name:string) => {
    getDownloadURL(ref(storage, name))
    .then((url) => {
      const xhr = new XMLHttpRequest()
      xhr.responseType = 'blob'
      xhr.onload = (event) => {
        const blob = xhr.response

        const urls = window.URL.createObjectURL(
          new Blob([blob]),
        )
        const link = document.createElement('a')!
        link.href = urls
        link.setAttribute(
          'download',
          title,
        )
        document.body.appendChild(link)
        link.click()
        link.parentNode!.removeChild(link)
      }
      xhr.open('GET', url)
      xhr.send()
    })
  }

  return (
    <>
    {isNewDay &&
      <Box sx={{mb:2}}>
        <SystemDate date={currentDate.format(date)} />
      </Box>
    }
    <Paper sx={{display:'flex', alignItems:'center', justifyContent:'space-between', mb:2, p:2}}>
      <Box sx={{display:'flex', alignItems:'center', overflow:'hidden'}}>
        {getIcon(contentType)}
        <Typography sx={{ml:2}} noWrap>
          {title}
        </Typography>
      </Box>
      <Box sx={{display:'flex', alignItems:'center'}}>
        <Typography>
          {currentDate.format(date)}
        </Typography>
        <Typography sx={{ml:2,mr:1}}>
          {getSize(size)}
        </Typography>
        <Tooltip title='Скачать'>
          <IconButton onClick={()=>dowloadFile('/'+title)}>
            <Download/>
          </IconButton>
        </Tooltip>
      </Box>
    </Paper>
    </>
  )
}