import React, { useEffect } from 'react'
import { Route, Routes } from 'react-router-dom'
import { homeRoutes } from '../routes/routes'
import { Aside } from '../components/Aside'
import { Alert, Box, Grid, Snackbar } from '@mui/material'
import { MemoryBar } from '../components/MemoryBar'
import { grey } from '@mui/material/colors'
import { Container } from '@mui/system'
import { NavBar } from '../components/NavBar'
import { useAppDispatch } from '../hooks/useAppDispatch'
import { useAppSelector } from '../hooks/useAppSelector'
import { setIsShowAlert, setIsShowModal } from '../store/reducers/uploadFileSlice'
import { ModalUpLoad } from '../components/ModalUpLoad'
import { getDownloadURL, getMetadata, listAll, ref } from 'firebase/storage'
import { storage } from '../firebase'
import { addFiles, setLoading, } from '../store/reducers/fileSlice'
import { ITableFile } from '../interface/ITableFile'

export const Home = () => {
  const dispatch = useAppDispatch()
  const {files:upoadFiles, isModalShow, isShowAlert} = useAppSelector(state=>state.uploadFiles)
  const {files} = useAppSelector(state=>state.files)

  const bgMain = grey[200]

  const handleClose = (event?: React.SyntheticEvent | Event, reason?: string) => {
    if (reason === 'clickaway') {
      return
    }
    dispatch(setIsShowAlert(false))
  }

  useEffect(() => {
    if(files.length === 0){
      dispatch(setLoading(true))
      
      const filesListRef = ref(storage, '/')
      listAll(filesListRef)
        .then(res=>{

          if(res.items.length === 0){
            dispatch(setLoading(false))
            return
          }

          res.items.map((file:any)=>{
            const filetRef = ref(storage, '/'+file._location.path_)
            getDownloadURL(filetRef)
              .then((url) => {
                getMetadata(filetRef)
                  .then((metadata) => {
                    const item:ITableFile = {
                      id: file._location.path_,
                      title: file._location.path_,
                      url:url,
                      size:metadata.size,
                      timeCreated:metadata.timeCreated,
                      contentType:metadata.contentType!,
                    }
                    dispatch(addFiles(item))
                    dispatch(setLoading(false))
                  })
            })
          })
        })
    }
  }, [])

  return (
    <>
    <Grid container>
      <Grid item xs={3} md={3} lg={2} xl={2}>
        <Aside/>
      </Grid>
      <Grid item xs={6} md={6} lg={8} xl={8} sx={{background:`${bgMain}`, height:'100vh'}}>
        <Container maxWidth='xl' disableGutters>
          <NavBar/>
          <Box sx={{p:2}}>
            <Routes>
              {homeRoutes.map(({url, component: Component})=><Route key={url} path={url} element={<Component/>}/>)}
            </Routes>
          </Box>
        </Container>
      </Grid>
      <Grid item xs={3} md={3} lg={2} xl={2}>
        <MemoryBar/>
      </Grid>
    </Grid>
    {isModalShow &&
        <ModalUpLoad open={isModalShow} setOpen={()=>dispatch(setIsShowModal(false))}/>
      }
      {isShowAlert &&
        <Snackbar open={isShowAlert} autoHideDuration={2000} onClose={handleClose} anchorOrigin={{ vertical:'top', horizontal:'center' }}>
          <Alert onClose={handleClose} severity="success" sx={{ width: '100%' }}>
            {upoadFiles.length > 1 ? 'Файлы успешно отправились!' : 'Файл успешно отправился!'}
          </Alert>
        </Snackbar>
      }
    </>
  )
}