import { createSlice, PayloadAction } from "@reduxjs/toolkit"
import { cookieIsAgreeName } from "../../constants/app";
import { getCookie } from "../../helper/functions"

interface initialState {
  name:string | null;
  isAgree:boolean;
}

const initialState:initialState = {
  name:null,
  isAgree: getCookie(cookieIsAgreeName) === 'true' ? true : false
}
const userSlice = createSlice({
  name:'user',
  initialState,
  reducers:{
    setAgree(state, action:PayloadAction<boolean>){
      state.isAgree = action.payload
    }
  }
})

export const {setAgree} = userSlice.actions
export default userSlice.reducer